import { Component, OnInit } from "@angular/core";
import { FormGroup } from '@angular/forms';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { IOptionsPsAgGrid } from 'src/app/commonSRC/psComponents/ps-base/ps-ag-grid/ps-ag-grid.component.interfaces';
import { PsKeyinDateComponent } from "src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-field-keyin/ps-keyin-date/ps-keyin-date.component";
import { PsKeyinInputComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-field-keyin/ps-keyin-input/ps-keyin-input.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { OmniBasePage } from "../omni-base/omni-base.page";
import { ConstantCommon } from './../../commonSRC/psServices/models/common-constant';
import { IOptionsPsTemplateView } from './../../commonSRC/psServices/models/ps-common-interface';

@Component({
    selector: 'redemption-history',
    templateUrl: './redemption-history.page.html'
})
export class RedemptionHistoryPage extends OmniBasePage implements OnInit {
    formGroup = new FormGroup({});
    templateViewOptions: IOptionsPsTemplateView = {
        group: this.formGroup,
        disableLoading: true
    };
    redemptionHistoryOptions: IOptionsPsAgGrid = {
        fcName: 'redemptionHistory',
        group: this.formGroup,
        gridOptions: {
            url: CommonBussinessConstant.RETURN_REDEMPTIONS_URL,
            defaultColDef: {
                flex: CommonBussinessConstant.GRID_FLEX,
                minWidth: CommonBussinessConstant.GRID_MIN_WIDTH,
                sortable: true,
                resizable: true,
            },
            rowSelection: 'multiple',
            applyPagination: true,
            paginationPageSize: CommonBussinessConstant.GRID_PAGE_SIZE,
            cacheBlockSize: CommonBussinessConstant.GRID_CACHE_BLOCK_SIZE,
            cacheOverflowSize: CommonBussinessConstant.GRID_CACHE_OVERFLOW_SIZE,
            maxConcurrentDatasourceRequests: CommonBussinessConstant.GRID_MAX_CONC_DATA,
            infiniteInitialRowCount: CommonBussinessConstant.GRID_INFINITE_INITIAL_ROW_COUNT,
            maxBlocksInCache: CommonBussinessConstant.GRID_MAX_BLOCKS_IN_CACHE
        },
        enableEdit: false,
        enableDelete: false,
        enableSort: true,
        enableFilter: true,
        headerCheckboxSelection: false,
        headerCheckboxSelectionFilteredOnly: false,
        checkboxSelection: false
    };

    constructor(public commonService: PsCommonService, public logger: LoggerService, public omniPull: OmniPullService) {
        super();
    }

    async ngOnInit() {
        super.init();
        this.baseFormGroup = this.formGroup;
        this.redemptionHistoryOptions.columnComponents = [
            {
                columnNameKey: 'redemption_account_key',
                propertyName: 'redemptionAccountNumber',
                component: PsKeyinInputComponent,
                componentOptions: {},
                componentType: 'input'
            },
            {
                columnNameKey: 'redeemed_points_key',
                propertyName: 'pathRedeemedPoints',
                component: PsKeyinInputComponent,
                componentOptions: {},
                componentType: 'input'
            },
            {
                columnNameKey: 'reward_key',
                propertyName: 'pathRedemptionReward',
                component: PsKeyinInputComponent,
                componentOptions: {},
                componentType: 'input'
            },
            {
                columnNameKey: 'redeemed_amount_key',
                propertyName: 'pathAmount',
                component: PsKeyinInputComponent,
                componentOptions: {},
                componentType: 'input'
            },
            {
                columnNameKey: 'redemption_date_key',
                propertyName: 'pathRedemptiondate',
                component: PsKeyinDateComponent,
                componentOptions: {},
                componentType: 'date'
            }
        ];
        const res = await this.omniPull.getParamValOf('DefaultDateFormat').catch(err => this.logger.log(err));
        if (res) {
            this.redemptionHistoryOptions.dateFormat = res.DefaultDateFormat ? res.DefaultDateFormat : ConstantCommon.DEFAULT_DATE_FORMAT;
        }
    }
}